import React, {useState} from "react";
import useCustomLogin from "./useCustomLogin";
import KakaoLoginComponent from "./KakaoLoginComponent";

//  
const initState = {
    email: '',
    password: ''
}

function LoginComponent(props) {
    //    state
    const [loginParam, setLoginParam] = useState({...initState})
    const {doLogin, moveToPath} = useCustomLogin()

    //     
    const handleChange = (e) => {
        //  loginParam  
        loginParam[e.target.name] = e.target.value
        //  loginParam state 
        setLoginParam({...loginParam})
    }

    //      
    const handleClickLogin = (e) => {
        // Redux login  dispatch  
        // dispatch(login(loginParam))
        console.log('Login attempt with:', loginParam);


        doLogin(loginParam).then(data => {
            if(data.error) {
                alert("   ")
            }else {
                moveToPath("/Main")
            }
        })
    }


    return (
        <div className="loginContainer">
            <div className="loginTitle"></div>
            <div className="loginDescription">pop`in   ?</div>
            <div className="loginLabel"></div>
            <input
                className="loginInput"
                name="email"
                type={'text'}
                value={loginParam.email}
                onChange={handleChange}
                placeholder=""
            />
            <div className="loginLabel"></div>
            <input
                className="loginInput"
                name="password"
                type={'password'}
                value={loginParam.password}
                onChange={handleChange}
                placeholder=""
            />
            <div className="guideText"> ?</div>
            <div>
                <button className="loginButton"
                        onClick={handleClickLogin}></button>
            </div>
            <div className='signupPromptSmall'>  </div>
            <div className="kakaoContainer">
                <KakaoLoginComponent/>
            </div>
            <div className="signupPrompt">
                <div>  ? <a className="signupLink" href='../Signup'></a></div>
            </div> 
        </div>
    );
} 

export default LoginComponent;