import React, { useState, useEffect } from 'react';
import Modal from 'react-modal'
import './css/Userinfo.css';
import { useSelector, useDispatch } from 'react-redux';
import { modifyMember } from './api/userApi';
import { login } from './slices/loginSlice';

const initState = {
    id: '',
    email: '',
    nickname: '',
    name: '',
}

export default function UserInfo() {
    const [member, setMember] = useState(initState)
    const loginInfo = useSelector(state => state.loginSlice)
    const dispatch = useDispatch();

    useEffect(() => {
        setMember({...loginInfo})
    }, [loginInfo]);

    const handleChange = (e) => {
        member[e.target.name] = e.target.value
        setMember({...member})
    }
    
    const handleClickModify = async () => {
        try {
            await modifyMember(member);
            alert('   .');

            dispatch(login(member));

            //     .
            setMember(prevState => ({
                ...prevState,
                nickname: member.nickname
                
            }));
        } catch (error) {
            console.error('     :', error);
            alert('     .');
        }
    }

    const [point, setPoint] = useState(0); //    
    const [isOpen, setIsOpen] = useState(false); //     

    const openModal = () => {
        setIsOpen(true);
    };

    const closeModal = () => {
        setIsOpen(false);
    };

    const modalStyles = {
        overlay: {
            backgroundColor: "rgba(0, 0, 0, 0.5)", 
            zIndex: "9999",
        },
        content: { 
            width: "35%",
            height: "auto",
            margin: "auto",
            borderRadius: "4px",
            boxShadow: "0 2px 4px rgba(0, 0, 0, 0.2)",
        },
    };

    // useEffect       .
    useEffect(() => {
        //     .     .
        //   API         .
        const fetchedPoint = 1234567; //   
        setPoint(fetchedPoint); //     .
    }, []);

    return (
        <div className="userinfo-container">
            <div className="userinfo-content">
                <img src={require('../../assets/images/ic_bigPerson.png')} alt='person icon' className="userinfo-image" />
                <span className="userinfo-nickname">{member.nickname}</span>
            </div>
            <div className="point-info">
                <div style={{ color: '#B1B5C3', fontSize: '12px', marginBottom: '10px' }}> </div>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                    <span className="point" style={{ color: '#000000', fontSize: '16px' }}>{point.toLocaleString()}</span>
                    <button className="charge-btn"></button>
                </div>
            </div>
            <div style={{ display: 'flex', width: '100%'}}>
                <span style={{ flex: 4 }}>
                    <div className="infoLabel"></div>
                    <input
                        className="infoInput"
                        name="email"
                        type={'text'}
                        placeholder=""
                        value={member.email}
                        readOnly
                    />
                </span>
                <span style={{ flex: 1 }}></span>
                <span style={{ flex: 4 }}>
                    <div className="infoLabel"></div>
                    <input
                        className="infoInput"
                        name="name"
                        type={'text'}
                        placeholder=""
                        value={member.name}
                        readOnly
                    />
                </span>
            </div>
            <div className="infoLabel"></div>
            <div className="inputWithButton">
                <input
                    className="infoInput"
                    name="nickname"
                    type={'text'}
                    placeholder=""
                    value={member.nickname}
                    onChange={handleChange}
                />
                <button className="changeButton" onClick={handleClickModify} style={{ display: 'inline-block', marginLeft: '30px'}}> </button>
            </div>
            <div className="infoLabel"></div>
            <button className="changeButton" onClick={openModal}> </button>

            <Modal isOpen={isOpen} onRequestClose={closeModal} style={modalStyles}>
                <span className='modalContent'>
                <div className='modal-cancle'>
                    <img
                        src={require('../../assets/images/ic_cancle.png')}
                        alt='cancle'
                        onClick={closeModal} //   onClick  
                        style={{ cursor: 'pointer'}} //        
                    />
                </div>
                <div className='logoContainer'><img className='modal-logo' 
                        src={require('../../assets/images/bigLogo.png')} 
                        alt='logo'/></div>
                <div className="infoLabel"></div>
                <div className="inputWithButton-m">
                    <input
                        className="infoInput1"
                        name="email"
                        type={'text'}
                        placeholder=""
                    />
                    <button className="checkButton"> </button>
                </div>
                <div className="infoLabel"></div>
                <div className="inputWithButton-m">
                    <input
                        className="infoInput1"
                        name="email"
                        type={'text'}
                        placeholder=" ."
                    />
                    <button className="checkButton"
                            style={{width: '26%'}}></button>
                </div>
                <div className="infoLabel"> </div>
                    <input
                        className="infoInput2"
                        name="pw"
                        type={'password'}
                        placeholder=" "
                    />
                    <div className="guideText">8  .</div>
                    <div className="infoLabel"> </div>
                    <input
                        className="infoInput2"
                        name="pwCheck"
                        type={'password'}
                        placeholder=""
                    />
                <button className='checkButton' 
                        style={{ display: 'flex', width: '100%', justifyContent: 'center', marginTop: '20px'}}
                        onClick={closeModal}></button>
                </span>
            </Modal>
        </div>
    );
}
