import React, { useState, useEffect } from "react";
import ChatMessage from "./ChatMessage";
import { Client } from '@stomp/stompjs';
import "./css/ChatScreen.css";

const ChatScreen = () => {
    const [messages, setMessages] = useState([]);
    const [client, setClient] = useState(null);
    const [inputMessage, setInputMessage] = useState("");

    const handleMessage = (message) => {
        console.log(" : ", message.body); //   
        const body = JSON.parse(message.body);
    
        // ID   
        const existingMessage = messages.find(msg => msg.id === body.id);
        if (!existingMessage) {
            setMessages(prevMessages => {
                const updatedMessages = [...prevMessages, body];
                console.log(" messages : ", updatedMessages); //    
                return updatedMessages;
            });
        }
    };

    useEffect(() => {
        if (!client) {
            const newClient = new Client({
                brokerURL: "wss://www.plick.shop/ws", //-> 
                debug: function (str) {
                    console.log(str);
                },
                reconnectDelay: 5000,
                heartbeatIncoming: 4000,
                heartbeatOutgoing: 4000,
            });

            newClient.onConnect = () => {
                console.log("  ");
                setClient(newClient);

                newClient.subscribe('/sub/public', (message) => {
                    handleMessage(message);
                });
            };

            newClient.onStompError = (error) => {
                console.error("  :", error);
            };

            newClient.activate();
        }

        return () => {
            if (client && client.connected) {
                client.deactivate();
            }
        };
    }, [client]);

    useEffect(() => {
        console.log(" messages : ", messages); //    
    }, [messages]);

    const sendMessage = () => {
        if (client && client.connected) {
            const chatMessage = {
                type: "MESSAGE",
                content: inputMessage,
                sender: " ",
                time: new Date().toISOString()
            };

            console.log(" : ", chatMessage); //    
            client.publish({
                destination: '/pub/chat.sendMessage',
                body: JSON.stringify(chatMessage),
            });
            setInputMessage("");
        } else {
            console.error("STOMP  ");
        }
    };

    return (
        <div className="chat-screen">
            <div className="chat-messages">
                {messages.map((message, index) => {
                    console.log(" : ", message); //   
                    return (
                        <ChatMessage
                            key={index}
                            nickname={message.sender}
                            message={message.content}
                            time={new Date(message.time).toLocaleString()} // ISO 8601   
                        />
                    );
                })}
            </div>
            <div className="chat-input">
                <input
                    className="chat-input-box"
                    type="text"
                    placeholder=" ."
                    value={inputMessage}
                    onChange={(e) => setInputMessage(e.target.value)}
                />
                <button
                    className="chat-input-button"
                    aria-label=""
                    onClick={sendMessage}
                >
                    
                </button>
            </div>
        </div>
    );
};

export default ChatScreen;
